import React, { Component } from "react";
import {
  Modal,
  Button,
  Container,
  Row,
  Col,
  InputGroup,
  FormControl,
} from "react-bootstrap";
import { Link } from "react-router-dom";
import axios from "axios";
import VideoPlayer from "../components/VideoPlayer";
import Comments from "../components/Comments";
import Reactions from "../components/Reactions";
import VideoBG from "../components/VideoBG";
import "./Main.css";

class Main extends Component {
  constructor(props) {
    super(props);
    this.handleShow = this.handleShow.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.tokenChange = this.tokenChange.bind(this);
    this.postIdChange = this.postIdChange.bind(this);
    this.after = "";
    this.state = {
      status: false,
      modalShow: false,
      token: "",
      postId: "",
      backgroundUrl: "",
      fontSize: "14",
      avatarSize: 50,
      url: "",
      playing: true,
      controls: false,
      volume: 0.8,
      muted: false,
      loop: false,
      timeout: 3,
      like: "0",
      love: "0",
      haha: "0",
      sad: "0",
      wow: "0",
      angry: "0",
      videoBG: "",
      comments: [],
      showIcons: {
        like: true,
        love: true,
        haha: true,
        sad: true,
        wow: true,
        angry: true,
      },
    };
  }

  handleClose() {
    this.setState({ modalShow: false });
  }

  handleShow() {
    this.setState({ modalShow: true });
  }
  async getReactions() {
    try {
      let result = await axios.get(
        `https://fbtoolsvn.herokuapp.com/api/v1/reactions?postId=${this.state.postId}`
      );
      if (result) {
        let counts = result.data.data.reduce((p, c) => {
          let type = c.type;
          if (!p.hasOwnProperty(type)) {
            p[type] = 0;
          }
          p[type]++;
          return p;
        }, {});
        this.setState({
          like: counts.LIKE,
          love: counts.LOVE,
          haha: counts.HAHA,
          sad: counts.SORRY,
          wow: counts.WOW,
          angry: counts.ANGER,
        });
      }
    } catch (error) {
      console.log(error);
    }
  }
  async getComments() {
    try {
      let result = await axios.get(
        `https://fbtoolsvn.herokuapp.com/api/v1/comments?postId=${this.state.postId}&limit=20`
      );
      if (result.data) {
        this.setState({
          comments: result.data.data,
        });
      }
    } catch (error) {
      console.log(error);
    }
  }
  handleClick = () => {
    this.setState((state) => {
      if (state.status) {
        clearInterval(this.timer);
      } else {
        this.timer = setInterval(
          () => {
            this.getReactions();
            this.getComments();
          },
          state.timeout < 1 ? 3000 : state.timeout * 1000
        );
      }
      return { status: !state.status };
    });
  };

  componentWillUnmount() {
    clearInterval(this.timer);
  }
  tokenChange = (e) => {
    this.setState({
      token: e.target.value,
    });
  };
  bgChange = (e) => {
    this.setState({
      backgroundUrl: e.target.value,
    });
  };
  fontSizeChange = (e) => {
    this.setState({
      fontSize: parseFloat(e.target.value),
    });
  };
  avatarSizeChange = (e) => {
    this.setState({
      avatarSize: parseFloat(e.target.value),
    });
  };

  urlChange = (e) => {
    this.setState({
      url: e.target.value,
    });
  };
  videoBGChange = (e) => {
    this.setState({
      videoBG: e.target.value,
    });
  };
  postIdChange = (e) => {
    this.setState({
      postId: e.target.value,
    });
  };

  timeoutChange = (e) => {
    this.setState({
      timeout: e.target.value,
    });
  };
  savefbdata = (val, val2) => {
    this.setState(
      {
        token: val,
        postId: val2,
      },
      () => {
        this.handleClick();
      }
    );
  };
  load = (url) => {
    this.setState({
      url,
      played: 0,
      loaded: 0,
      pip: false,
    });
  };
  toggleControls = () => {
    const url = this.state.url;
    this.setState(
      {
        controls: !this.state.controls,
        url: null,
      },
      () => this.load(url)
    );
  };
  toggleLoop = () => {
    this.setState({ loop: !this.state.loop });
  };
  toggleMuted = () => {
    this.setState({ muted: !this.state.muted });
  };
  setVolume = (e) => {
    this.setState({ volume: parseFloat(e.target.value) });
  };
  playPause = () => {
    this.setState({ playing: !this.state.playing });
  };
  chooseFile() {
    document.querySelector("#chooseFile").click();
  }
  chooseFile2() {
    document.querySelector("#bgVideoFile").click();
  }
  handleChangeFile = (file) => {
    this.setState({
      url: URL.createObjectURL(file),
    });
  };
  handleChangeFile2 = (file) => {
    this.setState({
      videoBG: URL.createObjectURL(file),
    });
  };

  setIconShow = (type) => {
    this.setState({
      showIcons: {
        ...this.state.showIcons,
        [type]: !this.state.showIcons[type],
      },
    });
  };

  render() {
    const {
      like,
      love,
      haha,
      sad,
      wow,
      angry,
      token,
      postId,
      status,
      videoBG,
      modalShow,
      backgroundUrl,
      playing,
      timeout,
      url,
      fontSize,
      controls,
      volume,
      muted,
      loop,
      avatarSize,
      showIcons,
    } = this.state;
    const customFont = { fontSize: fontSize };
    return (
      <div
        className="live-main"
        style={{ backgroundImage: `url(${backgroundUrl})` }}
      >
        <div className="live-controls">
          <Button
            variant="primary"
            className="btn-control"
            onClick={this.handleShow}
          ></Button>
        </div>
        <Modal show={modalShow} size="lg" onHide={this.handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Controls</Modal.Title>
            <Link to="/dualposts" className="ml-2 btn btn-danger">
              Dual Posts
            </Link>
          </Modal.Header>
          <Modal.Body>
            <Container>
              <h5>Facebook Connect</h5>
              <Row>
                <Col md={12} className="mb-3">
                  <InputGroup className="mb-3">
                    <InputGroup.Prepend>
                      <InputGroup.Text>Post ID</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                      placeholder="1010500792634..."
                      aria-label="Post ID"
                      value={postId}
                      onChange={this.postIdChange}
                    />
                  </InputGroup>
                  <InputGroup>
                    <InputGroup.Prepend>
                      <InputGroup.Text>
                        Request Time (Default: 3s)
                      </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                      type="number"
                      value={timeout}
                      onChange={this.timeoutChange}
                      placeholder="Set time each get comments and reactions (Default: 3s)"
                    />
                    <InputGroup.Append>
                      <Button
                        variant="primary"
                        className="btn-load"
                        onClick={() => this.savefbdata(token, postId)}
                      >
                        {status ? "Pause" : "Load"}
                      </Button>
                    </InputGroup.Append>
                  </InputGroup>
                </Col>
              </Row>
              <h5>Live Customize</h5>
              <Row className="mb-3">
                <Col md={6}>
                  <InputGroup>
                    <InputGroup.Prepend>
                      <InputGroup.Text id="basic-addon1">
                        Background
                      </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                      placeholder="Ex: https://i.imgur.com/G2....jpg"
                      aria-label="Background"
                      value={backgroundUrl}
                      onChange={this.bgChange}
                    />
                  </InputGroup>
                </Col>
                <Col md={6} className="mb-3">
                  <InputGroup>
                    <InputGroup.Prepend>
                      <InputGroup.Text id="basic-addon1">
                        Font Size ({fontSize}px)
                      </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                      className="slider"
                      type="range"
                      max={40}
                      aria-label="Font Size"
                      value={fontSize}
                      onChange={this.fontSizeChange}
                    />
                  </InputGroup>
                  <InputGroup>
                    <InputGroup.Prepend>
                      <InputGroup.Text id="basic-addon1">
                        Avatar Size ({avatarSize}px)
                      </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                      className="slider"
                      type="range"
                      max={50}
                      min={1}
                      aria-label="Avatar Size"
                      value={avatarSize}
                      onChange={this.avatarSizeChange}
                    />
                  </InputGroup>
                </Col>
                <Col md={12}>
                  <InputGroup className="mb-3">
                    <InputGroup.Prepend>
                      <InputGroup.Text id="basic-addon3">
                        Video Background(.mp4)
                      </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                      placeholder="Ex: https:https://sample-videos.com/..._bunny_720p_10mb.mp4"
                      aria-label="Background"
                      value={videoBG}
                      onChange={this.videoBGChange}
                    />
                    <InputGroup.Append>
                      <Button
                        variant="primary"
                        className="btn-load"
                        onClick={() => this.chooseFile2()}
                      >
                        Choose File
                      </Button>
                      <FormControl
                        type="file"
                        id="bgVideoFile"
                        aria-label="Video/Audio File"
                        onChange={(e) =>
                          this.handleChangeFile2(e.target.files[0])
                        }
                        hidden
                      />
                    </InputGroup.Append>
                  </InputGroup>
                </Col>
                <Col md={12}>
                  <InputGroup>
                    <InputGroup.Prepend>
                      <InputGroup.Text id="basic-addon1">
                        Video/Audio Link
                      </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                      placeholder="Ex: https://youtube.com/..."
                      aria-label="Video/Audio Link"
                      value={url}
                      onChange={this.urlChange}
                    />
                    <InputGroup.Append>
                      <Button
                        variant="primary"
                        className="btn-load"
                        onClick={() => this.chooseFile()}
                      >
                        Choose File
                      </Button>
                      <FormControl
                        type="file"
                        id="chooseFile"
                        aria-label="Video/Audio File"
                        onChange={(e) =>
                          this.handleChangeFile(e.target.files[0])
                        }
                        hidden
                      />
                    </InputGroup.Append>
                  </InputGroup>
                </Col>
              </Row>
              <Row>
                <Col md={12}>
                  <InputGroup.Prepend>
                    <Button
                      variant="primary"
                      className="btn-load"
                      onClick={this.playPause}
                    >
                      {playing ? "Pause" : "Play"}
                    </Button>
                    <InputGroup.Checkbox
                      checked={controls}
                      onChange={this.toggleControls}
                    />
                    <FormControl value={"Controls"} disabled className="mr-2" />
                    <InputGroup.Checkbox
                      checked={muted}
                      onChange={this.toggleMuted}
                    />
                    <FormControl value={"Mute"} disabled className="mr-2" />
                    <InputGroup.Checkbox
                      checked={loop}
                      onChange={this.toggleLoop}
                    />
                    <FormControl value={"Loop"} disabled className="mr-2" />
                    <InputGroup.Text id="basic-addon1">Volume</InputGroup.Text>
                    <FormControl
                      className="slider"
                      type="range"
                      aria-label="Volume"
                      min={0}
                      max={1}
                      step="any"
                      value={volume}
                      onChange={this.setVolume}
                    />
                  </InputGroup.Prepend>
                </Col>
              </Row>
              <Row className="my-2">
                <Col md={12}>
                  <InputGroup.Prepend>
                    <InputGroup.Text id="basic-addon1">Icon</InputGroup.Text>
                    <InputGroup.Checkbox
                      checked={showIcons.like}
                      onChange={() => this.setIconShow("like")}
                    />
                    <FormControl value={"Like"} disabled />
                    <InputGroup.Checkbox
                      checked={showIcons.love}
                      onChange={() => this.setIconShow("love")}
                    />
                    <FormControl value={"Love"} disabled />
                    <InputGroup.Checkbox
                      checked={showIcons.haha}
                      onChange={() => this.setIconShow("haha")}
                    />
                    <FormControl value={"Haha"} disabled />
                    <InputGroup.Checkbox
                      checked={showIcons.sad}
                      onChange={() => this.setIconShow("sad")}
                    />
                    <FormControl value={"Sad"} disabled />
                    <InputGroup.Checkbox
                      checked={showIcons.wow}
                      onChange={() => this.setIconShow("wow")}
                    />
                    <FormControl value={"Wow"} disabled />
                    <InputGroup.Checkbox
                      checked={showIcons.angry}
                      onChange={() => this.setIconShow("angry")}
                    />
                    <FormControl value={"Angry"} disabled />
                  </InputGroup.Prepend>
                </Col>
              </Row>
            </Container>
          </Modal.Body>
          <Modal.Footer>
            <span>
              © 2019 made with ♡ by <a href="https://fb.com/5515886">Lê Hậu</a>
            </span>
          </Modal.Footer>
        </Modal>
        <div className="videoBG">
          <div className="overlay"></div>
          <VideoBG videoBG={videoBG} />
        </div>
        <div className="main-area">
          <VideoPlayer
            url={url}
            playing={playing}
            controls={controls}
            volume={volume}
            muted={muted}
            loop={loop}
          />
          <Reactions
            like={showIcons.like && like}
            love={showIcons.love && love}
            haha={showIcons.haha && haha}
            sad={showIcons.sad && sad}
            wow={showIcons.wow && wow}
            angry={showIcons.angry && angry}
          />
          <div className="comments">
            {this.state.comments.map((comment, key) => (
              <Comments
                customFont={customFont}
                avatarSize={avatarSize}
                key={key}
                imageSrc={`https://graph.facebook.com/${comment.user.id}/picture`}
                name={comment.user.name}
                message={comment.message}
              />
            ))}
          </div>
        </div>
      </div>
    );
  }
}

export default Main;
