import React, { Component } from "react";

class Comments extends Component {
  render() {
    return (
      <div
        className="comment_item"
        style={{ gridTemplateColumns: `${this.props.avatarSize}px 1fr` }}
      >
        <div
          className="avatar"
          style={{
            width: this.props.avatarSize || 50,
            height: this.props.avatarSize || 50,
          }}
        >
          <img srcSet={this.props.imageSrc} alt="Avatar" />
        </div>
        <div className="content" style={this.props.customFont}>
          <span className="name">{this.props.name} </span>
          <span className="message">{this.props.message}</span>
        </div>
      </div>
    );
  }
}

export default Comments;
